.header__box {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  align-items: center;
  color: #1C274C;
  padding-top: 2.5rem;
  padding-bottom: 2.5rem;
}
@media (max-width: 1740px) {
  .header__box {
    grid-template-rows: repeat(2, 1fr);
  }
}
@media (max-width: 652px) {
  .header__box {
    grid-template-columns: repeat(3, 1fr);
    justify-items: center;
  }
}
.header__burger {
  display: none;
  width: 56px;
  height: 56px;
  background-color: #004293;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border-radius: 50%;
  gap: 10px;
  padding: 10px;
  margin-right: 48px;
  grid-area: 1/1/2/2;
}
.header__burger .line {
  width: 100%;
  height: 3.5px;
  background-color: #FFFFFF;
  border-radius: 2px;
}
@media (max-width: 992px) {
  .header__burger {
    display: flex;
  }
}
@media (max-width: 652px) {
  .header__burger {
    margin-right: 0;
  }
}
.header__brand {
  display: flex;
  width: fit-content;
  grid-area: 1/1/2/3;
  justify-content: center;
  align-items: center;
  gap: 48px;
}
@media (max-width: 1740px) {
  .header__brand {
    grid-area: 1/2/2/3;
  }
}
@media (max-width: 652px) {
  .header__brand {
    grid-area: 1/2/2/3;
  }
}
.header__brand-phone {
  position: relative;
}
@media (max-width: 992px) {
  .header__brand-phone {
    display: none;
  }
}
.header__brand-phone::before {
  content: "";
  background-image: url("../img/phone.svg");
  width: 20px;
  height: 20px;
  position: absolute;
  top: 0;
  left: -25px;
  background-size: contain;
}
.header__brand-logo {
  width: 127px;
  height: 102px;
  min-width: 76px;
  min-height: 61px;
}
.header__navbar {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 48px;
  grid-area: 1/4/2/9;
  margin-right: 48px;
}
@media (max-width: 1740px) {
  .header__navbar {
    grid-area: 2/1/3/11;
    justify-content: space-between;
    text-align: center;
    gap: 16px;
  }
  .header__navbar li {
    width: 100%;
    margin: 0 auto;
  }
  .header__navbar-links {
    width: 100%;
    justify-content: space-between;
  }
}
@media (max-width: 992px) {
  .header__navbar {
    display: none;
  }
}
.header__navbar-links {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 24px;
}
.header__navbar-link {
  transition: 0.3s all;
  border-left: 1px solid #1C274C;
  padding-left: 24px;
  font-weight: 500;
}
.header__navbar-link:first-of-type {
  border: none;
  position: relative;
  margin-right: 16px;
}
.header__navbar-link:first-of-type a:not(.link):hover::after {
  transform: rotate(180deg);
}
.header__navbar-link:first-of-type a:not(.link)::after {
  content: "";
  width: 16px;
  height: 16px;
  background-image: url("../img/alt-bottom.svg");
  background-size: contain;
  position: absolute;
  top: 3px;
  margin-left: 10px;
}
.header__navbar-link a:not(.link) {
  text-wrap: nowrap;
}
.header__navbar-link a:not(.link):hover {
  transform: translateY(-10px);
}
.header__call-btn {
  font-weight: 400;
  background-color: #004293;
  color: #FFFFFF;
  height: 72px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 60px;
  font-size: 20px;
  gap: 24px;
  padding: 8px 8px 8px 32px;
  box-shadow: 0px 4px 16px 0px rgba(0, 0, 0, 0.15);
  width: fit-content;
  grid-area: 1/9/2/10;
  margin-right: 48px;
}
.header__call-btn-text {
  display: flex;
  flex-direction: column;
  text-wrap: nowrap;
}
.header__call-btn-subtext {
  font-size: 14px;
}
@media (max-width: 652px) {
  .header__call-btn {
    grid-area: 2/1/3/4;
    width: 100%;
    margin: 0;
  }
}
.header__cart-btn {
  width: 72px;
  padding: 20px;
  background-color: #004293;
  border-radius: 50%;
  position: relative;
  grid-area: 1/10/2/11;
}
.header__cart-btn img {
  width: 30px;
  height: 30px;
}
.header__cart-btn::after {
  content: "99";
  width: 36px;
  height: 36px;
  position: absolute;
  background-color: #FFFFFF;
  opacity: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  right: 0;
  border-radius: 50%;
}
@media (max-width: 652px) {
  .header__cart-btn {
    grid-area: 1/3/2/4;
  }
}
.header .dropdown {
  position: absolute;
  padding: 49px 20px 49px 32px;
  width: auto;
  display: flex;
  justify-content: center;
  background-color: #FFFFFF;
  border-radius: 24px;
}
.header .dropdown__links {
  display: flex;
  flex-direction: column;
  gap: 34px;
  font-size: 16px;
  padding: 0;
  padding-right: 70px;
  border-right: 1px solid #FAFAFE;
}
.header .dropdown__links a {
  width: 100%;
}
.header .dropdown__link {
  position: relative;
  border-bottom: 1px solid #FAFAFE;
  padding-bottom: 17px;
}
.header .dropdown__link:first-of-type .dropdown__sublinks {
  display: flex;
  position: absolute;
  flex-direction: column;
  gap: 34px;
  font-size: 16px;
  padding: 0 70px 0 0;
  left: 220px;
  top: 0;
}
.header .dropdown__link:hover::after {
  margin-right: -50px;
}
.header .dropdown__link::after {
  content: "";
  width: 16px;
  height: 16px;
  background-image: url("../img/alt-bottom.svg");
  background-size: contain;
  position: absolute;
  top: 3px;
  right: 0;
  margin-right: -40px;
  transform: rotate(270deg);
  transition: 0.3s all;
}

.info {
  padding: 160px 0px;
  position: relative;
}
.info__circle {
  width: 502px;
  height: 502px;
  top: 0;
  left: 0;
  color: white;
  background-color: #004293;
  border-radius: 50%;
  position: absolute;
}
.info__title {
  font-weight: 700;
  font-size: 48px;
  color: #1C274C;
}
.info__title .emphasized {
  color: #004293;
}